<template>
  <container :title="title">
    <list :show-del="true">
      <thead slot="thead">
      <tr>
        <th><checkbox v-model="checked" :checked="tgCheck"/></th>
        <th>{{lang.id}}</th>
        <th>{{lang.ip}}</th>
        <th>{{lang.user}}</th>
        <th>{{lang.country}}</th>
        <th>{{lang.province}}</th>
        <th>{{lang.city}}</th>
        <th>{{lang.distrity}}</th>
        <th>{{$t('status.createTime')}}</th>
        <th>{{$t('operation.name')}}</th>
      </tr>
      </thead>
      <tbody slot="tbody">
      <tr v-for="vol in list">
        <td><checkbox  v-model="checkbox" :checked="checked" :value="vol.id"/></td>
        <td>{{vol.id}}</td>
        <td><span class="label label-success">{{vol.ip}}</span></td>
        <td>{{vol.username}} / {{vol.nickname}}</td>
        <td>{{vol.country}}</td>
        <td>{{vol.province}}</td>
        <td>{{vol.city}}</td>
        <td>{{vol.distrity}}</td>
        <td>{{vol.create_time}}</td>
        <v-td type="opt" :value="vol.id"/>
      </tr>
      </tbody>
    </list>
  </container>
</template>

<script>
  import {Container,List,Checkbox, VTd} from "@/components"

  export default {
    components: {VTd, List, Container,Checkbox},
    data(){
      return{
        title:this.$t('login_log.index'),
        lang:this.$t('login_log'),
        checked:false,
        tgCheck:false,
      }
    },
    computed:{
      list(){
        return this.$store.state.table.list
      },
      checkbox:{
        get(){
          return this.$store.state.table.checkbox
        },
        set(value){
          this.$store.commit('table/SET_CHECKBOX',value)
        }
      }
    },
    watch:{
      checkbox(newValue){
        const length = newValue.length
        if (length === this.list.length){
          this.tgCheck = true
        }
        if (length === 0) {
          this.tgCheck = false
        }
      }
    },
    mounted(){
      const url = '/api/login_log/index'
      this.$store.commit('table/SET_URL', url)
      this.$store.dispatch('table/fetchList')
    }
  }
</script>



